
.mu-day-button{display:inline-block;background:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none;text-decoration:none;cursor:pointer;margin:0;padding:4px 0;font-size:inherit;font-weight:400;position:relative;border:10px;width:42px
}
.mu-day-button.disabled{opacity:.4
}
.mu-day-empty{font-weight:400;padding:4px 0;position:relative;width:42px
}
.mu-day-button-bg{position:absolute;top:0;left:4px;height:34px;background-color:#03a9f4;border-radius:50%;opacity:0;transform:scale(0);transition:all .45s cubic-bezier(.23, 1, .32, 1);width:34px
}
.mu-day-button.hover .mu-day-button-bg,.mu-day-button.selected .mu-day-button-bg{transform:scale(1)
}
.mu-day-button.hover .mu-day-button-bg{opacity:.6
}
.mu-day-button.selected .mu-day-button-bg{opacity:1
}
.mu-day-button-text{font-weight:400;position:relative;color:rgba(0,0,0,0.87)
}
.mu-day-button.now .mu-day-button-text{color:#03a9f4
}
.mu-day-button.hover .mu-day-button-text,.mu-day-button.selected .mu-day-button-text{color:#ffffff
}


