
.mu-snackbar{position:fixed;bottom:0;left:0;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;color:#ffffff;background-color:rgba(0,0,0,0.87);padding:0 24px;min-height:48px;width:100%;max-width:568px
}
.mu-snackbar-action{margin:0 -16px 0 24px
}
.mu-snackbar-message{display:-ms-flexbox;display:flex;-ms-flex:1;flex:1;padding-top:8px;padding-bottom:8px
}
@media only screen and (max-width:992px) and (min-width:601px){
.mu-snackbar{width:auto;min-width:288px;left:5%;bottom:7%
}
}
@media only screen and (min-width:993px){
.mu-snackbar{width:auto;min-width:8%;top:10%;right:7%;left:auto;bottom:auto;min-width:288px
}
}
.mu-snackbar-enter-active,.mu-snackbar-leave-active{transition:transform .4s cubic-bezier(.23, 1, .32, 1),opacity .4s cubic-bezier(.23, 1, .32, 1);-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.mu-snackbar-enter,.mu-snackbar-leave-active{transform:translate3d(0, 100%, 0);opacity:0
}


