
.mu-text-field-line{margin:0;height:1px;border:none;background-color:#e0e0e0;left:0;right:0;position:absolute
}
.mu-text-field.has-icon .mu-text-field-line{left:56px
}
.mu-text-field-line.disabled{height:auto;background-color:transparent;border-bottom:2px dotted rgba(0,0,0,0.3)
}
html.pixel-ratio-2 .mu-text-field-line{transform:scaleY(.5)
}
html.pixel-ratio-3 .mu-text-field-line{transform:scaleY(.33)
}
.mu-text-field-focus-line{margin:0;height:2px;border:none;background-color:#03a9f4;position:absolute;left:0;right:0;margin-top:-1px;transform:scaleX(0);transition:transform .45s cubic-bezier(.23, 1, .32, 1)
}
.mu-text-field.has-icon .mu-text-field-focus-line{left:56px
}
.mu-text-field-focus-line.focus{transform:scaleX(1)
}
html.pixel-ratio-2 .mu-text-field-focus-line.focus{transform:scaleX(1) scaleY(.5)
}
html.pixel-ratio-3 .mu-text-field-focus-line.focus{transform:scaleX(1) scaleY(.33)
}
.mu-text-field-focus-line.error{transform:scaleX(1);background-color:#f44336
}
html.pixel-ratio-2 .mu-text-field-focus-line.error{transform:scaleX(1) scaleY(.5)
}
html.pixel-ratio-3 .mu-text-field-focus-line.error{transform:scaleX(1) scaleY(.33)
}
html.pixel-ratio-2 .mu-text-field-focus-line{transform:scaleX(0) scaleY(.5)
}
html.pixel-ratio-3 .mu-text-field-focus-line{transform:scaleX(0) scaleY(.33)
}


