
.mu-tr{border-bottom:1px solid #e0e0e0;color:rgba(0,0,0,0.87);height:48px
}
.mu-tr:last-child{border-bottom:none
}
.mu-tr.selected{background-color:#f5f5f5
}
.mu-tr.hover{background-color:#eeeeee
}
.mu-tr.stripe{background-color:rgba(247,252,255,0.4)
}
.mu-tfoot .mu-tr{border-top:1px solid #e0e0e0
}
.mu-tr .mu-checkbox{vertical-align:middle
}
.mu-checkbox-col{width:72px
}


