
.mu-clock-number{display:inline-block;width:32px;height:32px;line-height:24px;position:absolute;top:10px;text-align:center;padding-top:5px;font-size:1.1em;pointer-events:none;border-radius:100%;box-sizing:border-box;transform:translate(0, 5px);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.mu-clock-number.selected{background-color:#03a9f4;color:#ffffff
}
.mu-clock-number.inner{width:28px;height:28px
}


