
.mu-step-label{height:72px;color:rgba(0,0,0,0.87);display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;font-size:14px;padding-left:14px;padding-right:14px
}
.mu-stepper-vertical .mu-step-label{height:64px
}
.mu-step-label.disabled{color:rgba(0,0,0,0.3);cursor:not-allowed
}
.mu-step-label.active{font-weight:500
}
.mu-step-label-icon-container{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;margin-right:8px;width:24px
}
.mu-step-label-icon{display:block;font-size:24px;width:24px;height:24px;color:#9e9e9e
}
.mu-step-label.disabled .mu-step-label-icon{color:#9e9e9e
}
.mu-step-label.completed .mu-step-label-icon,.mu-step-label.active .mu-step-label-icon{color:#03a9f4
}
.mu-step-label-circle{width:20px;height:20px;font-size:12px;line-height:20px;text-align:center;overflow:hidden;border-radius:100%;background-color:#9e9e9e;color:#ffffff
}
.mu-step-label.disabled .mu-step-label-circle{background-color:#9e9e9e
}
.mu-step-label.completed .mu-step-label-circle,.mu-step-label.active .mu-step-label-circle{background-color:#03a9f4
}


