
.mu-step-button{border:10px;display:inline-block;cursor:pointer;text-decoration:none;margin:0;padding:0;outline:none;font-size:inherit;font-weight:inherit;transform:translate(0, 0);background-color:transparent;transition:all 450ms cubic-bezier(.23, 1, .32, 1) 0ms
}
.mu-stepper-vertical .mu-step-button{width:100%
}
.mu-step-button.hover{background-color:rgba(0,0,0,0.06)
}


