
.example-appbar{position:fixed;left:256px;right:0;top:0;width:auto;transition:all .45s cubic-bezier(.23, 1, .32, 1)
}
.example-appbar.nav-hide{left:0
}
.example-content{padding-top:56px;padding-left:256px;transition:all .45s cubic-bezier(.23, 1, .32, 1)
}
.example-content.nav-hide{padding-left:0
}
.content-wrapper{padding:48px 72px
}
@media (min-width:480px){
.example-content{padding-top:64px
}
}
@media (max-width:993px){
.example-appbar{left:0
}
.example-content{padding-left:0
}
.content-wrapper{padding:24px 36px
}
}


