
.mu-checkbox{position:relative;display:inline-block;height:24px;line-height:24px;cursor:pointer
}
.mu-checkbox input[type="checkbox"]{display:none
}
.mu-checkbox input[type="checkbox"]:checked+.mu-checkbox-wrapper .mu-checkbox-icon-uncheck{opacity:0;transition:opacity 650ms cubic-bezier(.23, 1, .32, 1) 150ms;color:#03a9f4
}
.mu-checkbox input[type="checkbox"]:checked+.mu-checkbox-wrapper .mu-checkbox-icon-checked{opacity:1;transform:scale(1);transition:opacity 0ms cubic-bezier(.23, 1, .32, 1),transform 800ms cubic-bezier(.23, 1, .32, 1)
}
.mu-checkbox input[type="checkbox"]:checked+.mu-checkbox-wrapper .mu-checkbox-ripple-wrapper{color:#03a9f4
}
.mu-checkbox *{pointer-events:none
}
.mu-checkbox.disabled{cursor:not-allowed
}
.mu-checkbox-wrapper{display:-ms-flexbox;display:flex;width:100%;height:24px;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between
}
.mu-checkbox-icon{width:24px;height:24px;vertical-align:middle;position:relative;margin-right:16px
}
.mu-checkbox.label-left .mu-checkbox-icon{margin-right:0;margin-left:16px
}
.mu-checkbox.no-label .mu-checkbox-icon{margin-left:0;margin-right:0
}
.mu-checkbox-label{color:rgba(0,0,0,0.87)
}
.mu-checkbox.disabled .mu-checkbox-label{color:rgba(0,0,0,0.3)
}
.mu-checkbox-icon-uncheck{position:absolute;left:0;top:0;opacity:1;transition:opacity 1s cubic-bezier(.23, 1, .32, 1) .2s;color:rgba(0,0,0,0.87)
}
.mu-checkbox.disabled .mu-checkbox-icon-uncheck{color:rgba(0,0,0,0.3)
}
.mu-checkbox-icon-checked{position:absolute;left:0;top:0;opacity:0;color:#03a9f4;transform:scale(0);transition:opacity 450ms cubic-bezier(.23, 1, .32, 1),transform 0ms cubic-bezier(.23, 1, .32, 1) 450ms
}
.mu-checkbox.disabled .mu-checkbox-icon-checked{color:rgba(0,0,0,0.3)
}
.mu-checkbox-ripple-wrapper{width:48px;height:48px;top:-12px;left:-12px
}
.mu-checkbox.label-left .mu-checkbox-ripple-wrapper{right:-12px;left:auto
}


