
.mu-divider{margin:0;height:1px;border:none;background-color:#e0e0e0;width:100%
}
.mu-divider.inset{margin-left:72px
}
.mu-divider.shallow-inset{margin-left:16px
}
html.pixel-ratio-2 .mu-divider{transform:scaleY(.5)
}
html.pixel-ratio-3 .mu-divider{transform:scaleY(.33)
}


