
.mu-slider{width:100%;position:relative;height:24px;margin-bottom:16px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;cursor:default
}
.mu-slider input[type="range"]{position:relative;display:block;height:20px;width:100%;margin:0;-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;background-position:center;background-size:100% 2px;background-repeat:no-repeat;outline:0;-ms-background-position-y:500px;z-index:2;opacity:0
}
.mu-slider input[type="range"]:active,.mu-slider input[type="range"]:focus{border:0;outline:0 none
}
.mu-slider input[type="range"]::-webkit-range-thumb{-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;border:none;outline:0;height:20px;width:20px;position:relative;opacity:0;border-radius:20px;z-index:2;cursor:pointer
}
.mu-slider input[type="range"]::-moz-range-track{width:100%;height:2px;border:none;outline:0
}
.mu-slider input[type="range"]::-moz-range-thumb{-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;border:none;outline:0;height:20px;width:20px;position:relative;opacity:0;border-radius:20px;z-index:2;cursor:pointer
}
.mu-slider input[type="range"]::-ms-track{width:100%;height:2px;cursor:pointer;background:transparent;border-color:transparent;color:transparent
}
.mu-slider input[type="range"]::-ms-thumb{-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;border:none;outline:0;height:20px;width:20px;position:relative;opacity:0;border-radius:20px;z-index:2;cursor:pointer
}
.mu-slider-track{position:absolute;height:2px;left:0;right:0;background-color:#bdbdbd
}
.mu-slider-fill{position:absolute;height:2px;width:100%;background-color:#03a9f4
}
.mu-slider.disabled .mu-slider-fill{background-color:#bdbdbd
}
.mu-slider-thumb{position:absolute;width:12px;height:12px;background-color:#03a9f4;color:#03a9f4;border-radius:50%;transform:translate(-50%, 0);transition:background 450ms cubic-bezier(.23, 1, .32, 1),border-color 450ms cubic-bezier(.23, 1, .32, 1),width 450ms cubic-bezier(.23, 1, .32, 1),height 450ms cubic-bezier(.23, 1, .32, 1)
}
.mu-slider.active .mu-slider-thumb{width:20px;height:20px
}
.mu-slider.zero .mu-slider-thumb,.mu-slider.disabled .mu-slider-thumb{border:2px solid #bdbdbd;color:#bdbdbd;background-color:#ffffff
}
.mu-slider.zero .mu-slider-thumb .mu-focus-ripple-wrapper,.mu-slider.disabled .mu-slider-thumb .mu-focus-ripple-wrapper{top:-14px;left:-14px
}
.mu-slider-thumb .mu-focus-ripple-wrapper{width:36px;height:36px;top:-12px;left:-12px
}


