
.mu-switch{position:relative;display:inline-block;height:24px;line-height:24px;cursor:pointer
}
.mu-switch input[type="checkbox"]{display:none
}
.mu-switch input[type="checkbox"]:checked+.mu-switch-wrapper .mu-switch-track{background-color:rgba(3,169,244,0.5)
}
.mu-switch input[type="checkbox"]:checked+.mu-switch-wrapper .mu-switch-thumb{background-color:#03a9f4;color:#03a9f4;left:18px
}
.mu-switch.disabled input[type="checkbox"]:checked+.mu-switch-wrapper .mu-switch-track{background-color:#bdbdbd
}
.mu-switch.disabled input[type="checkbox"]:checked+.mu-switch-wrapper .mu-switch-thumb{background-color:#e0e0e0
}
.mu-switch *{pointer-events:none
}
.mu-switch.disabled{cursor:not-allowed
}
.mu-switch-wrapper{display:-ms-flexbox;display:flex;width:100%;height:24px;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between
}
.mu-switch-container{width:38px;padding:4px 0 4px 2px;position:relative;margin-right:8px;transition:all 450ms cubic-bezier(.23, 1, .32, 1)
}
.mu-switch.label-left .mu-switch-container{margin-right:0;margin-left:8px
}
.mu-switch.no-label .mu-switch-container{margin-left:0;margin-right:0
}
.mu-switch-label{color:rgba(0,0,0,0.87)
}
.mu-switch.disabled .mu-switch-label{color:rgba(0,0,0,0.3)
}
.mu-switch-track{width:100%;height:14px;border-radius:30px;background-color:#bdbdbd;transition:all 450ms cubic-bezier(.23, 1, .32, 1)
}
.mu-switch.disabled .mu-switch-track{background-color:#bdbdbd
}
.mu-switch-thumb{position:absolute;top:1px;left:0;width:20px;height:20px;line-height:24px;color:rgba(0,0,0,0.87);background-color:#f5f5f5;border-radius:50%;box-shadow:rgba(0,0,0,0.117647) 0 1px 6px,rgba(0,0,0,0.117647) 0 1px 4px;transition:all 450ms cubic-bezier(.23, 1, .32, 1)
}
.mu-switch.disabled .mu-switch-thumb{background-color:#e0e0e0
}
.mu-switch-ripple-wrapper{height:200%;width:200%;top:-10px;left:-10px
}


