
.mu-text-field-label{line-height:20px;transition:all .45s cubic-bezier(.23, 1, .32, 1);z-index:1;cursor:text;transform:scale(.75);transform-origin:left top;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none
}
.mu-text-field.has-label .mu-text-field-label{top:8px;position:absolute
}
.mu-text-field.has-label .mu-text-field-label.float{transform:scale(1);top:38px;color:rgba(0,0,0,0.3)
}


