
.code-toolbar{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;height:56px;padding:0 24px;background-color:#e8e8e8;cursor:pointer
}
.code-toolBar-title{padding-right:16px;line-height:56px;font-size:20px;position:relative;text-overflow:ellipsis;white-space:nowrap;overflow:hidden;color:rgba(0,0,0,0.54)
}
.code-block-code-style{overflow:auto;max-height:1400px;transition:max-height 800ms cubic-bezier(.445, .05, .55, .95);margin:0
}
.code-block-code-style.hide{max-height:0
}
.code-block-description{background:#FFF;overflow:auto;padding:10px 20px 0;margin:0
}


