
.mu-toast{height:48px;line-height:48px;padding:0 24px;background-color:rgba(0,0,0,0.87);color:#ffffff;border-radius:24px;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;word-wrap:break-word;max-width:568px;width:100%;position:fixed;left:0;bottom:0
}
@media only screen and (max-width:992px) and (min-width:601px){
.mu-toast{width:auto;min-width:288px;left:5%;bottom:7%
}
}
@media only screen and (min-width:993px){
.mu-toast{width:auto;min-width:8%;top:10%;right:7%;left:auto;bottom:auto;min-width:288px
}
}
.mu-toast-enter-active,.mu-toast-leave-active{transition:transform .4s cubic-bezier(.23, 1, .32, 1),opacity .4s cubic-bezier(.23, 1, .32, 1);-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.mu-toast-enter,.mu-toast-leave-active{transform:translate3d(0, 100%, 0);opacity:0
}


