
.mu-picker{background:#ffffff;overflow:hidden;width:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;position:relative;-webkit-mask-box-image:-webkit-linear-gradient(bottom, transparent, transparent 5%, white 20%, white 80%, transparent 95%, transparent);-webkit-mask-box-image:linear-gradient(to top, transparent, transparent 5%, white 20%, white 80%, transparent 95%, transparent)
}
.mu-picker-center-highlight{height:36px;box-sizing:border-box;position:absolute;left:0;width:100%;top:50%;margin-top:-18px;pointer-events:none
}
.mu-picker-center-highlight:before{content:'';position:absolute;left:0;top:0;bottom:auto;right:auto;height:1px;width:100%;background-color:#e0e0e0;display:block;z-index:15
}
html.pixel-ratio-2 .mu-picker-center-highlight:before{transform:scaleY(.5)
}
html.pixel-ratio-3 .mu-picker-center-highlight:before{transform:scaleY(.33)
}
.mu-picker-center-highlight:after{content:'';position:absolute;left:0;bottom:0;right:auto;top:auto;height:1px;width:100%;background-color:#e0e0e0;display:block;z-index:15
}
html.pixel-ratio-2 .mu-picker-center-highlight:after{transform:scaleY(.5)
}
html.pixel-ratio-3 .mu-picker-center-highlight:after{transform:scaleY(.33)
}
.mu-picker-center-highlight:before{left:0;top:0;bottom:auto;right:auto
}
.mu-picker-center-highlight:after{left:0;bottom:0;right:auto;top:auto
}


