
.mu-linear-progress{position:relative;height:4px;display:block;width:100%;background-color:#bdbdbd;border-radius:2px;margin:0;overflow:hidden
}
.mu-linear-progress-indeterminate{position:absolute;top:0;bottom:0;width:40%;border-radius:2px;background-color:#03a9f4;animation:mu-linear-progress-animate 840ms cubic-bezier(.445, .05, .55, .95);animation-iteration-count:infinite
}
.mu-linear-progress-determinate{position:absolute;top:0;bottom:0;left:0;background-color:#03a9f4;transition:width .3s linear;border-radius:2px
}
@keyframes mu-linear-progress-animate{
0%{left:-40%
}
100%{left:100%
}
}


