
.mu-bottom-sheet{background-color:#ffffff;position:fixed;left:0;right:0;bottom:0
}
.mu-bottom-sheet-enter-active,.mu-bottom-sheet-leave-active{transition:transform .45s cubic-bezier(.23, 1, .32, 1)
}
.mu-bottom-sheet-enter,.mu-bottom-sheet-leave-active{transform:translate3d(0, 100%, 0)
}


