
.mu-clock{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:280px
}
.mu-clock-landspace{width:479px;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between
}
.mu-clock-container{height:280px;padding:10px;box-sizing:content-box;position:relative;padding-bottom:62px
}
.mu-clock-landspace .mu-clock-container{width:300px
}
.mu-clock-circle{position:absolute;top:20px;width:260px;height:260px;border-radius:100%;background-color:rgba(0,0,0,0.07)
}
.mu-clock-landspace .mu-clock-circle{left:50%;margin-left:-130px
}
.mu-clock-actions{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-pack:end;justify-content:flex-end;padding:8px;position:absolute;left:0;bottom:0;right:0
}


