
.mu-grid-tile{position:relative;display:block;height:100%;overflow:hidden
}
.mu-grid-tile>img{height:100%;transform:translateX(-50%);position:relative;left:50%
}
.mu-grid-tile-titlebar{position:absolute;left:0;right:0;bottom:0;height:48px;background:rgba(0,0,0,0.4);display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.mu-grid-tile.multiline .mu-grid-tile-titlebar{height:68px
}
.mu-grid-tile.top .mu-grid-tile-titlebar{bottom:auto;top:0
}
.mu-grid-tile-title-container{margin-left:16px;margin-right:0;color:#ffffff;-ms-flex:1;flex:1;overflow:hidden
}
.mu-grid-tile.action-left .mu-grid-tile-title-container{margin-right:16px;margin-left:0
}
.mu-grid-tile-action{-ms-flex-order:1;order:1
}
.mu-grid-tile.action-left .mu-grid-tile-action{-ms-flex-order:-1;order:-1
}
.mu-grid-tile-action .mu-icon{color:#ffffff
}
.mu-grid-tile-title{font-size:16px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;word-wrap:break-word
}
.mu-grid-tile-subtitle{font-size:12px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;word-wrap:break-word
}


