
.mu-menu-item-wrapper{font-size:16px;height:48px;line-height:48px;transition:all .45s cubic-bezier(.23, 1, .32, 1);color:rgba(0,0,0,0.87);position:relative;cursor:pointer
}
.mu-menu-destop .mu-menu-item-wrapper{height:32px;line-height:32px;font-size:15px
}
.mu-menu-item-wrapper.hover{background-color:rgba(0,0,0,0.1)
}
.mu-menu-item-wrapper.active{color:#ff4081
}
.mu-menu-item-wrapper.disabled{color:rgba(0,0,0,0.3);cursor:not-allowed
}
.mu-menu-item{padding:0 16px;position:relative;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center
}
.mu-menu-destop .mu-menu-item{padding:0 24px
}
.mu-menu-item.have-left-icon{padding-left:72px
}
.mu-menu-item-left-icon{position:absolute;top:0;left:4px;margin:12px;color:#757575
}
.mu-menu-destop .mu-menu-item-left-icon{top:4px;left:24px;margin:0
}
.mu-menu-item-right-icon{position:absolute;top:0;right:4px;margin:12px;color:#757575
}
.mu-menu-destop .mu-menu-item-right-icon{top:4px;right:24px;margin:0
}


