
.mu-picker-slot{-webkit-box-flex:1;-webkit-flex-shrink:1;-ms-flex:0 1 auto;-ms-flex-negative:1;flex-shrink:1;font-size:18px;overflow:hidden;position:relative;max-height:100%;text-align:center
}
.mu-picker-slot.mu-picker-slot-divider{color:rgba(0,0,0,0.87);display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;line-height:36px
}
.mu-picker-slot-wrapper.animate{transition:transform .45s cubic-bezier(.23, 1, .32, 1);-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.mu-picker-item{line-height:36px;padding:0 10px;font-size:20px;white-space:nowrap;position:relative;overflow:hidden;text-overflow:ellipsis;color:rgba(0,0,0,0.54);left:0;top:0;width:100%;box-sizing:border-box;transition-duration:.3s;-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.mu-picker-item.selected{color:rgba(0,0,0,0.87);transform:translate3d(0, 0, 0) rotateX(0)
}


