
.mu-calendar{color:rgba(0,0,0,0.87);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:310px
}
.mu-calendar-landspace{width:479px
}
.mu-calendar-container{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.mu-calendar-monthday-container{display:-ms-flexbox;display:flex;-ms-flex-line-pack:justify;align-content:space-between;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-direction:column;flex-direction:column;font-size:12px;font-weight:400;padding:0 8px;transition:all .45s cubic-bezier(.23, 1, .32, 1)
}
.mu-calendar-week{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-pack:justify;justify-content:space-between;font-weight:500;height:20px;line-height:15px;opacity:.5;text-align:center
}
.mu-calendar-week-day{width:42px
}
.mu-calendar-monthday{position:relative;overflow:hidden;height:214px
}
.mu-calendar-monthday-slide{position:absolute;height:100%;width:100%;top:0;left:0
}
.mu-calendar-actions{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-pack:end;justify-content:flex-end;margin:0;max-height:48px;padding:0
}
.mu-calendar-actions .mu-flat-button{min-width:64px;margin:4px 8px 8px 0
}
.mu-calendar-slide-next-enter-active,.mu-calendar-slide-next-leave-active,.mu-calendar-slide-prev-enter-active,.mu-calendar-slide-prev-leave-active{transition:transform 450ms cubic-bezier(.23, 1, .32, 1),opacity 450ms cubic-bezier(.23, 1, .32, 1);-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.mu-calendar-slide-next-enter{transform:translate3d(100%, 0, 0)
}
.mu-calendar-slide-next-leave-active{transform:translate3d(-100%, 0, 0);opacity:0
}
.mu-calendar-slide-prev-enter{transform:translate3d(-100%, 0, 0)
}
.mu-calendar-slide-prev-leave-active{transform:translate3d(100%, 0, 0);opacity:0
}


