
.mu-date-display{width:100%;font-weight:700;display:block;background-color:#03a9f4;border-top-left-radius:2px;border-top-right-radius:2px;border-bottom-left-radius:0;color:#ffffff;padding:20px
}
.mu-calendar-landspace .mu-date-display{width:165px;height:330px;float:left;border-top-right-radius:0;border-bottom-left-radius:2px
}
.mu-date-display-year{position:relative;overflow:hidden;margin:0;font-size:16px;font-weight:500;line-height:16px;height:16px;opacity:.7;transition:all .45s cubic-bezier(.23, 1, .32, 1);margin-bottom:10px
}
.mu-date-display.selected-year .mu-date-display-year{opacity:1
}
.mu-date-display-year-title{cursor:pointer
}
.mu-date-display-year.disabled .mu-date-display-year-title{cursor:not-allowed
}
.mu-date-display-year-title .mu-date-display.selected-year{cursor:default
}
.mu-date-display-monthday{position:relative;display:block;overflow:hidden;font-size:36px;line-height:36px;height:38px;transition:all .45s cubic-bezier(.23, 1, .32, 1);width:100%;font-weight:500
}
.mu-date-display.selected-year .mu-date-display-monthday{opacity:.7
}
.mu-calendar-landspace .mu-date-display-monthday{height:100%
}
.mu-date-display-slideIn-wrapper{position:absolute;height:100%;width:100%;top:0;left:0
}
.mu-date-display-monthday-title{cursor:default;width:100%;display:block
}
.mu-date-display.selected-year .mu-date-display-monthday-title{cursor:pointer
}
.mu-date-display-next-enter-active,.mu-date-display-next-leave-active,.mu-date-display-prev-enter-active,.mu-date-display-prev-leave-active{transition:transform 450ms cubic-bezier(.23, 1, .32, 1),opacity 450ms cubic-bezier(.23, 1, .32, 1);-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.mu-date-display-next-enter{transform:translate3d(0, -100%, 0);opacity:0
}
.mu-date-display-next-leave-active{transform:translate3d(0, 100%, 0);opacity:0
}
.mu-date-display-prev-enter{transform:translate3d(0, 100%, 0);opacity:0
}
.mu-date-display-prev-leave-active{transform:translate3d(0, -100%, 0);opacity:0
}


