
.mu-refresh-control{display:-ms-flexbox;display:flex;margin:0 auto;width:40px;height:40px;color:#03a9f4;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;background-color:#FFF;border-radius:50%;box-shadow:rgba(0,0,0,0.117647) 0 1px 6px,rgba(0,0,0,0.117647) 0 1px 4px;position:absolute;left:50%;margin-left:-18px;margin-top:24px;z-index:90
}
.mu-refresh-control .mu-icon{display:inline-block;vertical-align:middle
}
.mu-refresh-control-animate{transition:all .45s ease
}
.mu-refresh-control-hide{opacity:1;transform:translate3d(0, -63px, 0)
}
.mu-refresh-control-noshow{opacity:0;transform:scale(.01)
}
.mu-refresh-control-refreshing{transform:scale(1);opacity:1
}


