
.mu-dialog{position:fixed;left:50%;top:50%;transform:translate3d(-50%, -50%, 0);width:75%;max-width:768px;margin:0 auto;padding:0;background-color:#ffffff;border-radius:2px;font-size:16px;box-shadow:rgba(0,0,0,0.298039) 0 19px 60px,rgba(0,0,0,0.219608) 0 15px 20px
}
.mu-dialog-header{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between;padding:24px 24px 20px
}
.mu-dialog-header+.mu-dialog-body{padding-top:0
}
.mu-dialog-header.scrollable{border-bottom:1px solid #e0e0e0
}
.mu-dialog-title{-ms-flex:1;flex:1;font-size:22px;line-height:32px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;margin:0;font-weight:normal;color:rgba(0,0,0,0.87)
}
.mu-dialog-body{padding:24px 24px 20px;color:rgba(0,0,0,0.6)
}
.mu-dialog-footer{min-height:48px;padding:8px;text-align:right
}
.mu-dialog-footer .mu-raised-button+.mu-raised-button{margin-left:10px
}
.mu-dialog-footer.scrollable{border-top:1px solid #e0e0e0
}
.mu-dialog-scale-enter-active,.mu-dialog-scale-leave-active{transition:all .45s cubic-bezier(.23, 1, .32, 1)
}
.mu-dialog-scale-enter,.mu-dialog-scale-leave-active{opacity:0
}
.mu-dialog-scale-enter{transform:translate3d(-50%, -50%, 0) scale(1.1)
}
.mu-dialog-scale-leave-active{transform:translate3d(-50%, -50%, 0) scale(.8)
}


