
.mu-chip{border-radius:16px;line-height:32px;white-space:nowrap;display:-ms-inline-flexbox;display:inline-flex;-ms-flex-align:center;align-items:center;background-color:#e0e0e0;color:rgba(0,0,0,0.87);padding:0 12px;cursor:default
}
.mu-chip .mu-avatar:first-child{margin-left:-12px;margin-right:4px
}
.mu-chip.active{box-shadow:0 1px 6px rgba(0,0,0,0.12),0 1px 4px rgba(0,0,0,0.12)
}
.mu-chip.hover{background-color:#cecece;cursor:pointer
}
.mu-chip.hover .mu-chip-delete-icon{color:rgba(0,0,0,0.4)
}
.mu-chip-delete-icon{margin-right:-8px;margin-left:4px;color:rgba(0,0,0,0.26)
}


