
.mu-clock-pointer{height:40%;background-color:#03a9f4;width:2px;left:calc(49%);position:absolute;bottom:50%;transform-origin:center bottom 0;pointer-events:none
}
.mu-clock-pointer.inner{height:30%
}
.mu-clock-pointer-mark{box-sizing:content-box;background-color:#ffffff;border:4px solid #03a9f4;width:7px;height:7px;position:absolute;top:-5px;left:-6px;border-radius:100%
}
.mu-clock-pointer-mark.has-selected{display:none
}


