
.mu-dropDown-menu{display:inline-block;font-size:15px;height:48px;position:relative;transition:all .45s cubic-bezier(.23, 1, .32, 1);cursor:pointer;overflow:hidden
}
.mu-dropDown-menu.disabled{color:rgba(0,0,0,0.3);cursor:not-allowed
}
.mu-dropDown-menu-icon{position:absolute;right:16px;top:16px;color:#e0e0e0
}
.mu-dropDown-menu-text{padding-left:24px;padding-right:48px;line-height:56px;opacity:1;position:relative;color:rgba(0,0,0,0.87)
}
.mu-dropDown-menu.disabled .mu-dropDown-menu-text{color:rgba(0,0,0,0.3)
}
.mu-dropDown-menu-line{bottom:1px;left:0;margin:-1px 24px;right:0;position:absolute;height:1px;background-color:#e0e0e0;transition:all .45s cubic-bezier(.23, 1, .32, 1)
}
html.pixel-ratio-2 .mu-dropDown-menu-line{transform:scaleY(.5)
}
html.pixel-ratio-3 .mu-dropDown-menu-line{transform:scaleY(.33)
}


