
.mu-radio{position:relative;display:inline-block;height:24px;line-height:24px;cursor:pointer
}
.mu-radio input[type="radio"]{display:none
}
.mu-radio input[type="radio"]:checked+.mu-radio-wrapper .mu-radio-icon-uncheck{opacity:0;transform:scale(0);color:#03a9f4
}
.mu-radio input[type="radio"]:checked+.mu-radio-wrapper .mu-radio-icon-checked{opacity:1;transform:scale(1)
}
.mu-radio input[type="radio"]:checked+.mu-radio-wrapper .mu-radio-ripple-wrapper{color:#03a9f4
}
.mu-radio *{pointer-events:none
}
.mu-radio.disabled{cursor:not-allowed
}
.mu-radio-wrapper{display:-ms-flexbox;display:flex;width:100%;height:24px;-ms-flex-align:center;align-items:center;-ms-flex-pack:justify;justify-content:space-between
}
.mu-radio-icon{width:24px;height:24px;vertical-align:middle;position:relative;margin-right:16px
}
.mu-radio.label-left .mu-radio-icon{margin-right:0;margin-left:16px
}
.mu-radio.no-label .mu-radio-icon{margin-left:0;margin-right:0
}
.mu-radio-label{color:rgba(0,0,0,0.87);-ms-flex:1;flex:1;font-size:16px
}
.mu-radio.disabled .mu-radio-label{color:rgba(0,0,0,0.3)
}
.mu-radio-icon-uncheck{position:absolute;left:0;top:0;opacity:1;transition:all 450ms cubic-bezier(.23, 1, .32, 1);color:rgba(0,0,0,0.87)
}
.mu-radio.disabled .mu-radio-icon-uncheck{color:rgba(0,0,0,0.3)
}
.mu-radio-icon-checked{position:absolute;left:0;top:0;opacity:0;color:#03a9f4;transform:scale(0);transition:all 450ms cubic-bezier(.23, 1, .32, 1)
}
.mu-radio.disabled .mu-radio-icon-checked{color:rgba(0,0,0,0.3)
}
.mu-radio-ripple-wrapper{width:48px;height:48px;top:-12px;left:-12px
}
.mu-radio.label-left .mu-radio-ripple-wrapper{right:-12px;left:auto
}


